import React, { Component } from 'react';
import PropTypes from 'prop-types';

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultSidebarHeader extends Component {
  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <img src={'assets/img/avatars/8.jpg'} className="img-avatar" alt="Avatar"/>
        <div><strong>JOHN DOE</strong></div>
        <div className="text-muted"><small>Founder &amp; CEO</small></div>
        <div className="btn-group" role="group" aria-label="Button group with nested dropdown">
          <button type="button" className="btn btn-link">
            <i className="icon-settings"></i>
          </button>
          <button type="button" className="btn btn-link">
            <i className="icon-speech"></i>
          </button>
          <button type="button" className="btn btn-link">
            <i className="icon-user"></i>
          </button>
        </div>
      </React.Fragment>
    );
  }
}

DefaultSidebarHeader.propTypes = propTypes;
DefaultSidebarHeader.defaultProps = defaultProps;

export default DefaultSidebarHeader;



